$(document).ready(function(){
    $(function(){
        setTimeout(function(){
            $('body').removeClass('overflow-hidden');
            $('#loader').hide();
        },4000);
    });
    $('.one').hover(function(){$(this).siblings(".one").toggleClass('show')});
    $('.two').hover(function(){$(this).siblings(".two").toggleClass('show')});
    $('.three').hover(function(){$(this).siblings(".three").toggleClass('show')});
    $('.four').hover(function(){$(this).siblings(".four").toggleClass('show')});
    $('.five').hover(function(){$(this).siblings(".five").toggleClass('show')});
    $('.six').hover(function(){$(this).siblings(".six").toggleClass('show')});
    $('#showLeftMap').click(function(){$(this).siblings(".leftMap").addClass('show')});
    $('#showRightMap').click(function(){$(this).siblings(".rightMap").addClass('show')});
    $('#hideLeftMap').click(function(){$(this).parent().parent().parent().find(".leftMap").removeClass('show')});
    $('#hideRightMap').click(function(){$(this).parent().parent().parent().find(".rightMap").removeClass('show')});
    $('#showRightMap').hover(function(){$(this).parent().parent().find("#hideLeftMap").toggleClass('bounceNow')});
    $('#showLeftMap').hover(function(){$(this).parent().parent().find("#hideRightMap").toggleClass('bounceNow')});
    $('.team-image').hover(function(){$(this).parent().parent().find(".team-inner").toggleClass('show')});
    $('.nav-item').hover(function(){$('body').toggleClass('nav-hover')});
    $('.team-image').hover(function(){$(this).parent().parent().parent().parent().find(".team-list").toggleClass('offAll')});
    $('#testimonials').owlCarousel({
        loop: true,
        items: 2,
        touchDrag: false,
        mouseDrag: false,
        margin: 0,
        autoplay: false,
        dots:false,
        nav: true,
        autoplayTimeout: 8500,
        smartSpeed: 1000,
        responsive: {
            0: {
                items: 1
            },
            768: {
                items: 1
            },
            1170: {
                items: 2
            }
        }
    });
    $(".abouttextsection").click(function() {
        $('html, body').animate({
            scrollTop: $("#abouttextsection").offset().top - 60
        }, 500);
    });
    $(".teamSection").click(function() {
        $('html, body').animate({
            scrollTop: $("#teamSection").offset().top - 60
        }, 500);
    });
    $(".knowledgeMailSection").click(function() {
        $('html, body').animate({
            scrollTop: $("#knowledgeMailSection").offset().top - 60
        }, 500);
    });
    var animation = bodymovin.loadAnimation({
        name: "Logo Animation",
        container: document.getElementById('logoContainerHeader'),
        path: 'https://taylorhawkes.sanepress.com/logo-black.json',
        renderer: 'svg',
        loop: true,
        autoplay: true,
    });
    var animation = bodymovin.loadAnimation({
        name: "Logo Animation",
        container: document.getElementById('logoContainerFooter'),
        // path: 'https://labs.nearpod.com/bodymovin/demo/markus/isometric/markus2.json',
        path: 'https://taylorhawkes.sanepress.com/logo-black.json',
        renderer: 'svg',
        loop: true,
        autoplay: true,
    });
    var animation = bodymovin.loadAnimation({
        name: "Logo Animation",
        container: document.getElementById('logoContainerLoader'),
        path: 'https://taylorhawkes.sanepress.com/logo-black.json',
        renderer: 'svg',
        loop: true,
        autoplay: true,
    });
    var animation = bodymovin.loadAnimation({
        name: "Logo Animation",
        container: document.getElementById('logoContainerYellow'),
        path: 'https://taylorhawkes.sanepress.com/logo-yellow.json',
        renderer: 'svg',
        loop: true,
        autoplay: true,
    });
    var animation = bodymovin.loadAnimation({
        name: "Accounting Logo Animation",
        container: document.getElementById('logoContainerAccounting'),
        path: 'https://taylorhawkes.sanepress.com/accountingLogo.json',
        renderer: 'svg',
        loop: true,
        autoplay: true,
    });

});
$(window).on("scroll", function () {
    AOS.init();
    if ($(this).scrollTop() > 30) {$('#nextBG').toggleClass('fade-out');}
});
$(function () {
    AOS.init();
});
